// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import store from './store'
import router from './router'
import {
   Row,
   Col,
   Tab, 
   Tabs,
   Card,
   List,
   Icon,
   Swipe, 
   Field,
   Toast,
   Dialog,
   Button,
   NavBar,
   Stepper,
   Lazyload,
   Checkbox,
   SwipeItem,
   Tabbar,
   TabbarItem,
   SubmitBar,
   PullRefresh,
   Actionsheet,
   GoodsAction,
   GoodsActionBigBtn,
   GoodsActionMiniBtn
} from 'vant';

Vue.use(Row)
   .use(Col)
   .use(Tab)
   .use(Tabs)
   .use(Card)
   .use(Icon)
   .use(List)
   .use(Swipe)
   .use(Field)
   .use(Toast)
   .use(Dialog)
   .use(Button)
   .use(Tabbar)
   .use(NavBar)
   .use(Stepper)
   .use(Lazyload)
   .use(Checkbox)
   .use(SubmitBar)
   .use(SwipeItem)
   .use(TabbarItem)
   .use(PullRefresh)
   .use(Actionsheet)
   .use(GoodsAction)
   .use(GoodsActionBigBtn)
   .use(GoodsActionMiniBtn);

Vue.config.productionTip = false

