<template>
   <div>
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
         <Navbar title="商品详情"></Navbar>
         <div class="detail-swipe">
            <van-swipe>
               <van-swipe-item>
                  <img v-lazy="info.IMAGE1" width="100%" @click="openPreview(info.IMAGE1)" />
               </van-swipe-item>
               <van-swipe-item >
                  <img v-lazy="info.IMAGE1" width="100%" @click="openPreview(info.PICTURE_COMPERSS_PATH)" />
               </van-swipe-item>
            </van-swipe>
         </div>
         
         <div class="goods-info">
            <div class="goods-name">{{info.NAME}} </div>
            <div class="goods-price">￥{{info.PRESENT_PRICE.toFixed(2)}}</div>
            <div class="pro-addr" style="overflow: hidden">
               <span style="float:left;">快递：0.00</span>
               <span style="float:right;"><em>广东 广州 白云区</em><i></i></span>
            </div>
         </div>

         <van-tabs swipeable animated :line-width="160" sticky>
            <van-tab title="商品详情">
               <img v-for="(item,index) in info.DETAIL" :key="index" v-lazy="item" width="100%" @click="openPreview(item)">
            </van-tab>
            <van-tab title="评论" style="height: 400px;">
               <p style="text-align: center;margin-top: 30px;">评论模块开发中</p> 
            </van-tab>
         </van-tabs>

      </van-pull-refresh>

      <van-actionsheet v-model="isShow" safe-area-inset-bottom>
         <AddCart @close="isShow=false" :id="info.ID" :img="info.IMAGE1" :name="info.NAME" :price="info.PRESENT_PRICE"></AddCart>
      </van-actionsheet>

      <van-goods-action safe-area-inset-bottom>
         <van-goods-action-big-btn text="加入购物车" @click="isShow=true"/>
         <van-goods-action-big-btn primary text="立即购买" />
      </van-goods-action>
   </div>
</template>

<script>
import {goodsDetail} from '@/api/api'
import { ImagePreview } from 'vant';
import accounting from 'accounting'
import Navbar from '../public/Navbar'
import AddCart from '../public/addCart'
export default {
   data() {
      return {
         info: {},
         isShow: false,
         isLoading: false
      }
   },
   created(){
      this.detailInfo()
   },
   methods: {
      detailInfo(flag){
         goodsDetail({goodsId: this.$route.query.id})
         .then(res=>{
            if(res.data.code == 200){
               this.info = res.data.message
               this.info.DETAIL = this.info.DETAIL.match( /(http\S+.jpg)/ig)
            }else{
               this.$toast('详情数据获取失败')
            }
         })
         .catch(err=>{
            this.$toast('详情数据获取失败')
         })
      },
      openPreview(item){
         ImagePreview({
            images: [item],
            lazyLoad: true,
            showIndex: false
         })
      },
      onRefresh() {
         setTimeout(() => {
            this.isLoading = false
            this.detailInfo()
         }, 600);
         
      }
   },
   components: {
      Navbar,
      AddCart
   }
}
</script>

<style lang="scss" scoped>
.detail-swipe {
   max-height: 750px;
   overflow: hidden;
   margin-bottom: 20px;
   border-top: 1px solid #f5f5f5;
}
.goods-info {
   font-size: 30px;
   padding: 24px 20px;
   background: #fff;
   .goods-name {
      padding-left: 10px;
   }
   .goods-price {
      font-size: 48px;
      color: red;
      padding-top: 10px;
   }
   .pro-addr {
      color: #999;
      font-size: 26px;
      padding-left: 10px; 
      padding-top: 10px;
   }
}
.van-tabs {
   margin-top: 20px;
}
img {
   vertical-align: middle;
}
.van-goods-action,
.van-overlay,
.van-actionsheet {
   width: 750px;
   margin: 0 auto;
}


</style>


