<template>
   <div>
      <van-nav-bar :title="title" left-arrow @click-left="back" />
   </div>
</template>

<script>
export default {
   methods: {
      back(){
         this.$router.go(-1)
      }
   },
   props: {
      title: String
   }
}
</script>

<style lang="scss" scoped>
.van-nav-bar {
   width: 750px;
   margin: 0 auto;
}
</style>


