<template>
   <div class="footer">
      <router-link to="/">
         <van-icon name="wap-home" size="0.48rem" />
         <span>首页</span>        
      </router-link>
      <router-link to="/category">
         <van-icon name="bars" size="0.48rem"  />
         <span>分类</span>        
      </router-link>
      <router-link to="/cart">
         <van-icon name="cart" size="0.48rem" />
         <span>购物车</span>        
      </router-link>
      <router-link to="/person">
         <van-icon name="manager" size="0.48rem" />
         <span>我的</span>        
      </router-link>
   </div>
</template>

<script>
export default {
   data(){
      return {
         active: 0
      }
   }
}
</script>

<style lang="scss" scoped>
.footer {
   width: 750px;
   margin: 0 auto;
   height: 100px;
   background: #ffffff;
   border-top: 1px solid #ccc;
   position: fixed;
   z-index: 99999;
   left: 0;
   right: 0;
   bottom: 0;
   box-sizing: content-box;
   padding-bottom: constant(safe-area-inset-bottom);
   padding-bottom: env(safe-area-inset-bottom);
   overflow: hidden;
   display: flex;
   align-items: center;
   justify-content: space-between;
   &>a {
      height: 100%;
      flex: 1 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #7d848a;
      font-size: 24px;
      display: flex;
      flex-direction: column;
   }
   &>.router-link-exact-active {
      color: #e5017d;
   }
}
</style>

