const axios =require('axios');
const baseUrl = 'http://localhost:3000';

// 获取首页数据
export const homeData = () => {
   return axios.get(`https://www.easy-mock.com/mock/5c9726dad1eeba73b8527408/shop/shop`)
}
// 注册接口
export const register = params => {
   return axios.post(`${baseUrl}/users/register`,params)
}
// 登录接口
export const login = params => {
  return axios.post(`${baseUrl}/users/login`, params)
}
// 验证token
export const token = params => axios.post(`${baseUrl}/users/verifyToken`, params)

// 商品详情
export const goodsDetail = params => axios.post(`${baseUrl}/goods/detail`, params)

// 大类数据
export const category = () => axios.get(`${baseUrl}/goods/category`)

// 子类数据
export const categorySub = params => axios.post(`${baseUrl}/goods/categorySub`,params)

// 类别商品
export const subGoods = params => axios.post(`${baseUrl}/goods/subGoods`, params)




