<template>

   <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-row class="search-bar">
         <van-col span="3" class="icon">
            <van-icon name="location" size="0.6rem"/>
         </van-col>
         <van-col span="16" class="input">
            <input type="text" placeholder="商城">
         </van-col>
         <van-col span="5" class="btn">
            <van-button type="primary" size="mini">搜索</van-button>
         </van-col>
      </van-row>

      <div class="swipe-box">
         <van-swipe :autoplay="2000">
            <van-swipe-item v-for="(item, index) in banner" :key="index">
               <img v-lazy="item.image" width="100%" @click="goDetail(item.goodsId)"/>
            </van-swipe-item>
         </van-swipe>
      </div>
      
      <div class="category">
         <div class="item" v-for="(item,index) in category" :key="index">
            <img v-lazy="item.image" width="100%">
            <span>{{item.mallCategoryName}}</span>
         </div>
      </div>

      <div class="adverte">
         <img v-lazy="adverte" width="100%">
      </div>

      <div class="recommend">
         <h2>商品推荐</h2>
         <swiper :options="{slidesPerView: 'auto'}" style="border-top: 1px solid #ebebeb">  
            <swiper-slide class="recommend_box" v-for="(item,index) in recommend" :key="index"  style="border-right: 1px solid #ebebeb">
               <div class="Img" @click="goDetail(item.goodsId)">
                  <img v-lazy="item.image" :alt="item.goodsName" width="100%">
               </div>
               <p class="name" @click="goDetail(item.goodsId)">{{item.goodsName}}</p>
               <p class="mall-price" @click="goDetail(item.goodsId)">￥{{item.mallPrice.toFixed(2)}}</p>
               <p class="price" @click="goDetail(item.goodsId)">￥{{item.price.toFixed(2)}}</p>
            </swiper-slide>
         </swiper>
      </div>

      <div class="floor">
         <dl v-for="(item,index) in floors" :key="index">
            <dt>
            <i>{{index+1}}F</i>
            <span>{{item.name}}</span> 
            </dt>
            <div class="body">
               <dd>
                  <img v-lazy="item.body[0].image" width="100%" @click="goDetail(item.body[0].goodsId)">
               </dd>
               <dd>
                  <img v-lazy="item.body[1].image" width="100%" @click="goDetail(item.body[1].goodsId)" style="border-bottom: 1px solid #eaeaea">
                  <img v-lazy="item.body[2].image" width="100%" @click="goDetail(item.body[2].goodsId)">
               </dd>
               <dd v-for="(key,idx) in item.body.slice(3)" :key="idx">
                  <img v-lazy="key.image" alt="" width="100%" @click="goDetail(item.goodsId)">
               </dd>
            </div>
         
         </dl>
      </div>

      <div class="hot">
         <h3>热卖商品</h3>
         <van-list class="goods">
            <van-row>
               <van-col span="12" v-for="(item,index) in hotGoods" :key="index">
                  <img v-lazy="item.image" width="100%" @click="goDetail(item.goodsId)" /> 
                  <p class="name" @click="goDetail(item.goodsId)">{{item.name}}</p>
                  <p class="price" @click="goDetail(item.goodsId)">￥{{item.price.toFixed(2)}}</p>
               </van-col>
            </van-row>
         </van-list>
      </div>
   </van-pull-refresh>
</template>

<script>
import {homeData} from '@/api/api'
import accounting from 'accounting'
import 'swiper/dist/css/swiper.min.css'
import { swiper, swiperSlide } from "vue-awesome-swiper" 
export default {
   data() {
      return {
         banner: [],   
         category:[],
         adverte: "",
         recommend: [],
         floors: [],
         hotGoods: [],
         isLoading: false
      }
   },
   created(){
      this.getHomeData()
   },
   methods: {
      getHomeData(){
         homeData().then(res=> {
            if(res.status==200){
               console.log( res.data );
               this.banner    = res.data.data.slides;
               this.category  = res.data.data.category;
               this.adverte   = res.data.data.advertesPicture.PICTURE_ADDRESS;
               this.recommend = res.data.data.recommend;
               this.hotGoods  = res.data.data.hotGoods;
               this.floors    = [
                  {name: res.data.data.floorName.floor1, body: res.data.data.floor1},
                  {name: res.data.data.floorName.floor2,body: res.data.data.floor2},
                  {name: res.data.data.floorName.floor3, body: res.data.data.floor3}
               ]
            }else{
               this.$toast('获取数据失败')
            }
         })
         .catch(err => {
            this.$toast('获取数据失败')
         })
      },
      goDetail(id){
         if(id){
            this.$router.push({ path: '/goodsDetail',query: {id} })
         }
      },
      onRefresh() {
         setTimeout(() => {
            this.isLoading = false
            this.getHomeData()
         }, 600)
      }
   },
   components: {
      swiper,
      swiperSlide
   }
}
</script>

<style lang="scss" scoped>
.home {
   padding-bottom: 150px;
}
.search-bar {
   height: 90px;
   line-height: 90px;
   text-align: center;
   overflow: hidden;
   color: #ffffff;
   background-color: #e5017d;
   .van-icon {
      vertical-align: middle;   
   }
   input {
      display: inline-block;
      width: 100%;
      height: 52px;
      line-height: 52px;
      text-indent: 20px;
      font-size: 24px;
      background: #e5017d;
      border-bottom: 1px solid #ffffff;
   }
   input::-webkit-input-placeholder{
      color:#ffffff;
   }
   input::-moz-placeholder{   /* Mozilla Firefox 19+ */
      color:#ffffff;
   }
   input:-moz-placeholder{    /* Mozilla Firefox 4 to 18 */
      color:#ffffff;
   }
   input:-ms-input-placeholder{  /* Internet Explorer 10-11 */ 
      color:#ffffff;
   }

}

.swipe-box {
   max-height: 338px;
   overflow: hidden;
}
.category {
   background-color: #fff;
   margin: 0 12px 12px 12px;
   border-radius: 12px;
   display: flex;
   div {
      padding: 12px;
      font-size: 24px;
      text-align: center;
      flex: 1;
   }
}
.recommend {
   margin-top: 18px;
   background: #ffffff;
   h2 {
      color: #df825a;
      font-size: 26px;
      padding: 14px 22px 20px 22px;
   }
   .recommend_box {
      width: 250px;
      overflow: hidden;
      cursor: pointer;
      p.mall-price {
         font-size: 24px;
         color: red;
         margin-top: 8px;
         padding: 0 0 0 20px;
      }
      p.price {
         font-size: 22px;
         color: #bbb;
         text-decoration: line-through;
         padding: 0 0 14px 20px;
      }
      p.name {
         font-size: 24px;
         padding: 0 0 0 20px;
         overflow: hidden;
         text-overflow: ellipsis;
         white-space: nowrap;
      }
      &:last-child {
         border-right: none!important;
      }
   }
   .img {
      width: 100%;
      overflow: hidden;
      img {
         width: 100%;
         height: 100%;
      }
   }
  
}
.floor {
   width: 100%;
   overflow: hidden;
   dl {
      dt {
         height: 80px;
         display: flex;
         justify-content: center;
         align-items: center;
         font-size: 26px;
         i {
            color: #fff;
            background: #df825a;
            border-radius: 50%;
            width: 50px;
            height: 50px;
            text-align: center;
            line-height: 50px;
         }
         span {
            color: #df825a;
            vertical-align: middle;
            margin-left: 14px;
         }
      }

      .body {
         background: #fff;
         display: flex;
         flex-wrap: wrap;
      }

      dd{
         width: 50%;
         border-right: 1px solid #eaeaea;
         border-bottom: 1px solid #eaeaea;
         
      }
      dd:nth-child(even) {
         border-right: none;
         padding-right: 4px;
      }
   }
}
.hot {
   h3 {
      height: 80px;
      line-height: 80px;
      text-align: center;
      color: #df825a;
      font-size: 26px;
      font-weight: normal;
   }
   .goods {
      background: #fff;
      .van-col {
         border-bottom: 1px solid #eaeaea;
         border-right: 1px solid #eaeaea;
         padding: 20px 0;
      }
      .name {
         font-size: 24px;
         overflow: hidden;
         text-overflow: ellipsis;
         white-space: nowrap;
         text-indent: 22px;
         padding-right: 20px;
      }
      .price {
         font-size: 22px;
         text-indent: 20px;
         color: red;
      }
   }
}

</style>




