<template>
   <div class="addcart">
      <div class="info">
         <div class="left">
            <img :src="img" />
         </div>
         <div class="right">
            <p>{{name||'liyong'}}</p>
            <p style="color: red">￥{{ price.toFixed(2)}}</p>
            <p>已选择 {{num}} 份</p>
         </div>
      </div>
      <div class="num">
         <div class="left">
            数量
         </div>
         <div class="right">
            <span class="sub" @click="subNum">-</span>
            <span>{{num}}</span>
            <span class="add" @click="addNum">+</span>
         </div>
      </div>
      <button class="confirm" @click="confirm">确定</button>
   </div>
</template>

<script>
import accounting from 'accounting'
export default {
   data() {
      return {
         num: 1
      }
   },
   methods: {
      subNum(){
         this.num > 1 ? this.num-- : null;  
      },
      addNum(){
         this.num++;
      },
      confirm(){
         let cartInfo = sessionStorage.cartInfo ? JSON.parse(sessionStorage.cartInfo) : [];
         let obj = {id:this.id,num:this.num,img:this.img,name: this.name,price: this.price,checked: 1}
         let flag = cartInfo.findIndex(item=>item.id == obj.id)
   
         console.info(flag)

         if(flag==-1){
            cartInfo.push(obj);
         }else{
            cartInfo[flag].num+=this.num;
         }
         sessionStorage.cartInfo = JSON.stringify(cartInfo)
         this.$toast('添加购物车成功')   
         this.$emit('close')
         
      }
   },
   props: {
      id: String,
      img: String,
      name: String,
      price: Number
   }
}
</script>

<style lang="scss" scoped>
.addcart {
   width: 750px;
   padding-bottom: 200px;
   position: relative;
   margin: 0 auto;
   background: #fff;
   .info {
      display: flex;
      padding: 20px;
      font-size: 30px;
      .left {
         width: 200px;
         height: 200px;
         display: flex;
         justify-content: center;
         align-items: center;
         border: 1px solid #ebebeb;
         img {
            width: 170px;
            height: 170px
         }
      }
      .right {
         flex: 1 0 auto;
         padding-left: 20px;
         p {
            padding-top: 20px;
         }
         p:last-child {
            font-size: 27px;
         }
      }
      
   }
   .num {
      padding: 30px 20px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 30px;
      border-top: 1px solid #ebebeb;
      border-bottom: 1px solid #ebebeb;
      margin-top: 10px;
      .right {
         display: flex;
         border: 1px solid #ebebeb;
         span {
            width: 50px;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-right: 1px solid #ebebeb;
            font-size: 28px;
         }
         span:last-child {
            border: 0;
            
         }
         span:first-child:active,
         span:last-child:active {
            background: #ebebeb;
            color: #fff;
         }
      }
   }
   .confirm {
      width: 100%;
      height: 100px;
      background: #FF4444;
      border: 0;
      font-size: 32px;
      color: #fff;
      margin-top: 40px; 
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
   }
}
</style>


