<template>
   <div class="register">
      <Navbar title="登录"></Navbar>
      <div class="register-box">
         <van-field v-model="username" label="用户名" placeholder="请输入用户名" clearable />
         <van-field v-model="password" type="password" label="密码" placeholder="请输入密码" clearable />
         <van-button type="primary" @click="login" :loading="loading" loading-text="登录中" block>马上登录</van-button>
         <p><router-link to="/register">去注册</router-link></p>
      </div>
   </div>
</template>

<script>
import {login} from '@/api/api'
import Navbar from '../public/Navbar'
export default {
   data() {
      return {
         username: '',
         password: '',
         loading: false
      }
   },
   methods: {
      back(){
         this.$router.go(-1)
      },
      login(){

         let patrn=/^(\w){6,20}$/i;  
         
         if(this.username.trim == '' || this.password == ''){
            this.$dialog.alert({message: '用户名或密码不能为空'})
            return false;
         }

         if(!patrn.test(this.password)){
            this.$dialog.alert({message: '密码只能6-20个字母_数字_下划线'})
            return false;
         }

         this.loading = true;

         login({
            username: this.username.trim(),
            password: this.password.trim()
         })
         .then(res=>{
             
            if(res.data.code == 200){
               if(res.data.message){
                  localStorage.token = res.data.message;
                  setTimeout(()=>{
                     this.$router.push('/person')
                  },500)
               }else{
                  this.$toast('用户名或密码错误');
               } 
            }else{
               this.$toast("登录异常");
            }
            
            this.loading = false;
         })
         .catch(err=>{
            this.loading = false;
            this.$toast('登录异常')
         })
      }
   },
   components: {
      Navbar
   }
}
</script>

<style lang="scss" scoped>

.register-box {
   width: 90%;
   margin: 50px auto;
   .van-button {
      margin-top: 40px;
   }
   p {
      text-align: right;
      font-size: 24px;
      padding-right: 10px;
      padding-top: 10px;
      padding-bottom: 10px;
      a {
         text-decoration: underline;
      }
      
   }
}
</style>


