<template>
   <div class="category">
      <Navbar title="商品分类"></Navbar>
      <van-row>
         <van-col span="6" class="left">
            <ul>
               <li @click="toggleCategory(idx,item.ID)" :class="{on: current==idx}" v-for="(item,idx) in categoryList" :key="idx">
                  {{item.MALL_CATEGORY_NAME}}
               </li>
            </ul>
         </van-col>
         <van-col span="18" class="right">
            <van-tabs v-model="active" :ellipsis="false" swipeable :line-width="50">
               <van-tab style="font-size: 14px" :title="item.MALL_SUB_NAME"  v-for="(item,index) in categorySub" :key="index">
               </van-tab>
            </van-tabs>
            <div class="list-box">
               <van-list
                     v-model="loading"
                     :finished="finished"
                     finished-text="没有更多了"
                     @load="onLoad"
                  >
                     
                     <p style="height: 100px;margin-bottom: 20px;background: red" v-for="item in list" :key="item">{{item}}</p>
                  </van-list>
            </div>
         </van-col>
      </van-row>
   </div>
</template>

<script>
import { category,categorySub } from '@/api/api'
import Navbar from '../public/Navbar'
export default {
   data() {
      return {
         categoryList: [],
         categorySub: [],
         loading: false,
         finished: false,
         current: 0,
         active: 0,
         list: []
      }
   },
   created(){
      this.getCategory()
   },
   methods: {
      getCategory(){
         category()
         .then(res=>{
            if(res.data.code == 200){
               console.log(res.data.message)
               this.categoryList = res.data.message
               this.getCategorySub(this.categoryList[0].ID)
            }else{
               this.$toast('数据获取失败')
            }
         })
         .catch(err=>{
            this.$toast('数据获取失败')
         })
      },
      toggleCategory(idx,id){
         this.current = idx
         this.getCategorySub(id)
      },
      
      getCategorySub(id){
         
         categorySub({id})
         .then(res=>{
            if(res.data.code == 200){
               this.categorySub = res.data.message
               this.active = 0;
            }else{
               this.$toast('数据获取失败')
            }
         })
         .catch(err=>{
            this.$toast('数据获取失败');
         })
      },
      onLoad() {
         // 异步更新数据
         setTimeout(() => {
            for (let i = 0; i < 10; i++) {
               this.list.push(this.list.length + 1);
            }
            // 加载状态结束
            this.loading = false;

            // 数据全部加载完成
            if (this.list.length >= 40) {
               this.finished = true;
            }
         }, 3000);
      }
   },
   components: {
      Navbar
   }
}
</script>

<style lang="scss" scoped>
.category {
   position: absolute;
   left: 0;
   top: 0;
   right: 0;
   bottom: 0;
   .van-row {
      width: 750px;
      margin: 0 auto;
      height: calc(100% - 190px);
   }
   .left {
      background: #efefef;
      height: 100%;
      ul {
        height: 100%;
      }
      ul li {
         text-align: center;
         line-height: 80px;
         font-size: 26px;
         border-top: 1px solid #eaeaea;
         border-bottom: 1px solid #eaeaea;
         &.on {
            background: #fff;
         }
      }
   }
   .right {
      height: 100%;
      .list-box {
         width: 100%;
         height: calc(100% - 82.5px);
         overflow: scroll;
      }
   }
}
</style>


