<template>
   <div class="cart">
      <div v-if="cartInfo.length>0">
         <div class="cartItem" v-for="(item,index) in cartInfo" :key="index" >
            <div class="check">
               <van-checkbox v-model="item.checked"></van-checkbox>
            </div>
            <div class="info">
               <div class="left">
                  <img v-lazy="item.img" />
               </div>
               <div class="right">
                  <p>{{item.name}}</p>
                  <p>限时优惠活动</p>
                  <p style="color: red">￥{{ item.price}}</p>
                  <div class="num">
                     <span class="sub" @click="subNum(index)">-</span>
                     <span>{{item.num}}</span>
                     <span class="add" @click="addNum(index)">+</span>
                  </div>
               </div>
            </div>
         </div>
         <div class="submit-box">
            <div class="left">
               <van-checkbox v-model="checkedAll" @click="toggleAll(!checkedAll)"></van-checkbox>
               <span @click="clearCart">清空</span>
            </div>
            <div class="right">
               <i>合计：{{count}}</i>
               <div class="btn">提交订单</div>
            </div>
         </div>
      </div>
      <div class="empty" style="color: #ff4444" v-else>
         您的购物车没有商品
      </div>
   </div>
</template>

<script>
import Navbar from '../public/Navbar.vue'
import accounting from 'accounting';
export default {
   data(){
      return {
         cartInfo: [],
         checked: 1
      }
   },
   created() {
      this.getCartInfo()
   },
   computed: {
      count(){
         let count = 0;
         this.cartInfo.forEach(item=>{
            count += item.price*item.num
         })
         return count.toFixed(2);
      },
      checkedAll(){
         return this.cartInfo.every(item=>item.checked)
      }
   },
   methods: {
      getCartInfo(){
         if(sessionStorage.cartInfo){
            this.cartInfo = JSON.parse(sessionStorage.cartInfo)
         }
      },
      subNum(idx){
         this.cartInfo[idx].num > 1 ? this.cartInfo[idx].num-- : null;
         this.$set(this.cartInfo,idx,this.cartInfo[idx])  
      },
      addNum(idx){
         this.cartInfo[idx].num++;
         console.log(576767);
         this.$set(this.cartInfo,idx,this.cartInfo[idx])  
      },
      toggleAll(done){
         this.cartInfo.forEach(item=>item.checked = done)
      },
      clearCart(){
         var that = this
         this.$dialog.confirm({
            message: '您确认清空购物车吗',
            beforeClose(action,done){
               if(action == 'confirm'){
                  sessionStorage.removeItem('cartInfo');
                  that.cartInfo = [];   
               }
               done()
            }
         })
      }
   },
   components:{
      Navbar
   },
   beforeRouteEnter (to, from, next) {
      if(!localStorage.token){
         next({path: '/login'})
      }
   }
}
</script>

<style lang="scss" scoped>
.cart {
   padding-bottom: 250px;
}
.submit-box {
   width: 750px;
   background: #fff;
   position: fixed;
   left: 0;
   right: 0;
   bottom: 100px;
   height: 100px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   box-sizing: content-box;
   padding-bottom: constant(safe-area-inset-bottom);
   padding-bottom: env(safe-area-inset-bottom);
   .left {
      padding-left: 30px;
      display: flex;
      align-items: center;
      span {
         margin-left: 25px;
         font-size: 26px;      
         padding-top: 5px;
         color: #FF4444;
      }
   }
   .right{
      display: flex;
      align-items: center;
      i {
         margin-right: 20px;
         color: #FF4444;
      }
   }
   .btn {
      width: 220px;
      height: 100px;
      line-height: 100px;
      background: #FF4444;
      color: #fff;
      text-align: center;
      float: right;
   }
}
.empty {
   position: absolute;
   left: 0;
   right: 0;
   top: 0;
   bottom: 0;
   display: flex;
   justify-content: center;
   align-items: center;
}
.cartItem {
   background: #fff;
   margin-top: 20px;
   overflow: hidden;
   display: flex;
   .check {
      display: flex;
      align-items: center;
      width: 100px;
      justify-content: center;
   }
   .info {
      flex: 1 0 auto;
      display: flex;
      padding: 30px;
      font-size: 30px;
      padding-left: 0;
      .left {
         width: 170px;
         height: 170px;
         padding: 5px;
         display: flex;
         justify-content: center;
         align-items: center;
         border: 1px solid #eaeaea;
         img {
            width: 100%;
            height:100%;
         }
      }
      .right {
         flex: 1 0 auto;
         padding-left: 20px;
         p {
            padding-bottom: 20px;

         }
         p:first-child {
            font-weight: bold;
            font-size: 30px;
            margin-top: 4px;
         }
         p:nth-child(2) {
            font-size: 25px;
            color: red
         }
         p:last-child {
            font-size: 28px;
            font-weight: bold;
         }

         .num {
            width: 150px;
            display: flex;
            margin-top: 10px;
            border: 1px solid #ebebeb;
            float: right;
            margin-right: 20px;
            span {
               width: 50px;
               height: 50px;
               display: flex;
               justify-content: center;
               align-items: center;
               border-right: 1px solid #ebebeb;
               font-size: 24px;
            }
            span:last-child {
               border: 0;
            }
            span:first-child:active,
            span:last-child:active {
               background: #ebebeb;
               color: #fff;
               font-size: 30px;
            }
         }
      }
      
   }
   
}
</style>


